/* üåå Background Style */
body {
  background-image: url("/Music-background.jpg"); /* üü¢ Load from public/music.jpg */
  background-size: cover;
  background-position: center;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* üéß Main Player Container */
.player-container {
  max-width: 350px;
  background: rgba(0, 0, 0, 0.75);
  color: #fff;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  margin: 50px auto;
  box-shadow: 0 0 25px rgba(0, 255, 100, 0.3);
}

/* üñºÔ∏è Cover Image */
.cover {
  width: 220px;
  height: 220px;
  border-radius: 15px;
  object-fit: cover;
  margin-bottom: 15px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.6);
}

/* üìù Scrolling Song Title */
.scrolling-title {
  overflow: hidden;
  white-space: nowrap;
  margin: 15px 0;
  font-size: 20px;
  font-weight: bold;
  color: #00ff88;
}

.scrolling-title span {
  display: inline-block;
  animation: scrollText 8s linear infinite;
}

@keyframes scrollText {
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
}

/* üéöÔ∏è Seek Bar */
.seek-bar {
  width: 100%;
  margin: 10px 0;
  appearance: none;
  height: 6px;
  background: #0dd314;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
}

.seek-bar::-webkit-slider-thumb {
  appearance: none;
  height: 14px;
  width: 14px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 0 2px #000;
  cursor: pointer;
}

.seek-bar::-moz-range-thumb {
  height: 14px;
  width: 14px;
  background: #fff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

/* ‚è±Ô∏è Time Display */
.time {
  font-size: 14px;
  margin-bottom: 10px;
  color: #ccc;
}

/* ‚ñ∂Ô∏è Controls */
.controls button {
  font-size: 24px;
  background: none;
  border: none;
  color: white;
  margin: 0 12px;
  cursor: pointer;
  transition: transform 0.2s, color 0.3s;
}

.controls button:hover {
  color: #0dd314;
  transform: scale(1.2);
}

/* üîä Volume Control */
.volume-control {
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.volume-control input[type="range"] {
  width: 130px;
  cursor: pointer;
  accent-color: #0dd314;
}
